import React, { useState } from 'react';
import { Button, Popover, OverlayTrigger, Table } from 'react-bootstrap';
import { createBrowserHistory } from 'history';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

const Column = ({ children }) => (
  <div className="column">{children}</div>
);

function App() {
  const [state, updateState] = useState({
    data: null,
    selectedNodes: []
  });
  const reader = new FileReader();
console.log(state);
window.state = state;
  return (
    <div className="App">
      <form>
        <div className="form-group">
          <label class="title" htmlFor="exampleFormControlFile1">Choose the file to be analysed</label>
          <input type="file" 
          style={{ display: "none" }}
          className="form-control-file file-input" 
          id="exampleFormControlFile1" 
          placeholder="Choose file" 
          onChange={
            (event) => {
              reader.readAsText(event.target.files[0]);
              reader.onload = async (e) => { 
                updateState({ data: JSON.parse(e.target.result), selectedNodes: state.selectedNodes });
              };
            }
          } />
          <label for="exampleFormControlFile1" class="custom-file-upload">
            Upload Result File
        </label>
        </div>
      </form>  
      {
        state.data &&
        (
          <div style={{ maxWidth: '900px', margin: 'auto'}}>
          
          {state.data.run && (
            <div>
              <p>Run {state.data.run}</p>
              <p>Generation {state.data.generation}</p>
              <p>Iteration {state.data.iteration}</p>
              <p>Fitness {state.data.fitness}</p>
            </div>
          )}


          <div>
            <h1>Prediction Table</h1>
            <div>{JSON.stringify(state.data.predictionTable)}</div>

            <h1>Truth Table</h1>
            <div>{JSON.stringify(state.data.truthMatrix)}</div>
          </div> 

          <h1> Nodes:</h1>
          <Column>
            { [...Array(state.data.classifier.config.sizes.inputs)].map((node, index) => {
              return (
                <Button key={index + 1} variant="outline-primary">{index + 1}</Button>
                );
              }) }
          </Column>
          
          { [...Array(state.data.classifier.config.sizes.columns)].map((_, columnIndex) => {
            return (
              <Column>
                { [...Array(state.data.classifier.config.sizes.rows)].map((node, rowIndex) => {
                  const nodeIndex = rowIndex + 1 + state.data.classifier.config.sizes.inputs * columnIndex + state.data.classifier.config.sizes.inputs;
                  const isActive = state.data.classifier.activeNodes.includes(nodeIndex);
                  const isOutput = state.data.classifier.programOutputs.includes(nodeIndex);
                  const popover = (
                    <Popover id="popover-basic">
                      <Popover.Title as="h3">Node {nodeIndex}</Popover.Title>
                      <Popover.Content>
                        <p>
                        Function Gene: {(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 1 - state.data.classifier.config.sizes.inputs}<br />
                          Input Genes: {(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 2 - state.data.classifier.config.sizes.inputs}, {(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 3 - state.data.classifier.config.sizes.inputs}
                          (Nodes )<br/>
                          Parameter Genes: {(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 4 - state.data.classifier.config.sizes.inputs}, {(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 5 - state.data.classifier.config.sizes.inputs}
                        </p>
                        <p>
                        Function Value: {state.data.classifier.genes[(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node - state.data.classifier.config.sizes.inputs]}<br />
                          Input Values: {state.data.classifier.genes[(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 1 - state.data.classifier.config.sizes.genes_per_node]}, {state.data.classifier.genes[(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 2 - state.data.classifier.config.sizes.genes_per_node]}<br />
                          Parameter Values: {state.data.classifier.genes[(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 3- state.data.classifier.config.sizes.inputs]}, {state.data.classifier.genes[(nodeIndex - state.data.classifier.config.sizes.inputs) * state.data.classifier.config.sizes.genes_per_node + 4 - state.data.classifier.config.sizes.inputs]}
                        </p>
                      </Popover.Content>
                    </Popover>
                  );

                  return (
                    <OverlayTrigger placement="right" overlay={popover}>
                       {({ ref, ...triggerHandler }) => (
                        <Button ref={ref} key={nodeIndex} {...triggerHandler} variant={isOutput ? "warning" : isActive ? "outline-success" : "outline-light" } className={isActive ? 'active': ''}>{
                          nodeIndex
                        }</Button>
                        )}
                    </OverlayTrigger>
                  );
                  })}
              </Column>
            );
          }) }

          <h1>Genotype</h1>
          <Table striped bordered hover>
        
            <thead>
            
              <tr>
                <th>Computational Node</th>
                <th>Node</th>
                <th>Function Gene</th>
                {[...Array(state.data.classifier.config.sizes.connection_genes)].map((_, gene) => (<th>Connection Gene {gene + 1}</th>))}
                {[...Array(state.data.classifier.config.sizes.parameters)].map((_, gene) => (<th>Parameter Gene {gene + 1}</th>))}
              </tr>
            </thead>
            <tbody>
            {[...Array(state.data.classifier.config.sizes.nodes - state.data.classifier.config.sizes.inputs)].map((_, nodeIndex) => (
              <tr>
                <td style={{ border: '1px dashed #dee2e6', fontWeight: 'bold'}}>{nodeIndex + 1}</td>
                <td style={{ border: '1px dashed #dee2e6', fontWeight: 'bold'}}>{nodeIndex + 1 + state.data.classifier.config.sizes.inputs}</td>
                <td>{state.data.classifier.genes[(nodeIndex * state.data.classifier.config.sizes.genes_per_node)]}</td>
                {[...Array(state.data.classifier.config.sizes.connection_genes)].map((_, index) => (<td>{state.data.classifier.genes[nodeIndex * state.data.classifier.config.sizes.genes_per_node + index + 1]}</td>))}
                {[...Array(state.data.classifier.config.sizes.parameters)].map((_, index) => (<td>{state.data.classifier.genes[nodeIndex * state.data.classifier.config.sizes.genes_per_node + state.data.classifier.config.sizes.connection_genes + 1 + index]}</td>))}
              </tr>
            ))}
            </tbody>
          </Table>


          <h1>Active Nodes:</h1>
            <Table striped bordered hover style={{ border: 'none'}}>
            <tbody>
            { state.data.classifier.activeNodes.map((node, index) => {
              return (
                <td className="display-inline-block" key={index}>{node}</td>
                );
              }) }
            </tbody>
          </Table>

          <h1>Output Computational Nodes:</h1>
          <Table striped bordered hover style={{ border: 'none'}}>
            <tbody>
              { state.data.classifier.programOutputs.map((node, index) => {
                return (
                  <td className="display-inline-block" key={index}>{node}</td>
                  );
                }) }
            </tbody>
          </Table>


          <div className="list-genes">
          { state.data.classifier.genes.map((gene, index) => {
            if (index % state.data.classifier.config.sizes.genes_per_node === 0) {
                return (
                  <><br />
                  <span key={index} >
                    <Button variant="outline-secondary">{gene}</Button>
                  </span>
                  </>
                );
              } else {
                return (
                    <span key={index}><Button variant="outline-secondary">{gene}</Button></span>
                );
              } 
            }) }

          </div>

        </div>
        )
    }
    </div>
  );
}

export const history = createBrowserHistory({
  basename: process.env.PUBLIC_URL
});

export default App;